import { getRandomInt } from './utils';
import GameResult from './GameResult';

export default class Baseball {
    constructor(digit = 3) {
        this.digit = digit;
        this.problem = this.makeProblem(digit);
    }

    makeProblem() {
        // [Q-5] Ïù¥Î∂ÄÎ∂ÑÏùÑ ÏûëÏÑ±Ìï¥ Î≥¥ÏÑ∏Ïöî!
    }

    getResult(guess) {
        let strike = 0,
            ball = 0;

        this.problem.forEach((v, i) => {
            if (guess[i] === v) {
                strike++;
            } else if (this.problem.indexOf(guess[i]) > -1) {
                ball++;
            }
        });

        return new GameResult(this.digit, strike, ball);
    }
}
